import React from "react";
import {FaFacebookF, FaLinkedin, FaGithub} from 'react-icons/fa'
import { animateScroll as scroll } from "react-scroll";
import {
  FooterContainer,
  FooterWrap,
  FooterLinksContainer,
  FooterLinksWrapper,
  FooterLinkItems,
} from "./FooterElements";
import {
  SocialMedia,
  SocialLogo,
  SocialMediaWrap,
  WebsiteRights,
  SocialIcons,
  SocialIconLink,
  
} from "./FooterElements";

const Footer = () => {
  const toggleHome = () => {
    scroll.scrollToTop();
  };

  return (
    <FooterContainer id="more">
      <FooterWrap>
        <FooterLinksContainer>
          <FooterLinksWrapper>
            <FooterLinkItems>
            </FooterLinkItems>
          </FooterLinksWrapper>
        </FooterLinksContainer>
        <SocialMedia>
          <SocialMediaWrap>
            <SocialLogo to="/" onClick={toggleHome}>Portfolio</SocialLogo>
            <WebsiteRights>Designed by Peyton Prowse {new Date().getFullYear()}</WebsiteRights>
            <SocialIcons>
              <SocialIconLink href="//www.linkedin.com/in/peytonprowse/" target="_blank" aria-label="LinkedIn">
                <FaLinkedin />
              </SocialIconLink>
              <SocialIconLink href="//www.github.com/pmprowse" target="_blank" aria-label="GitHub">
                <FaGithub />
              </SocialIconLink>
              <SocialIconLink href="//www.facebook.com/peyton.prowse" target="_blank" aria-label="Facebook">
                <FaFacebookF />
              </SocialIconLink>
            </SocialIcons>
          </SocialMediaWrap>
        </SocialMedia>
      </FooterWrap>
    </FooterContainer>
  );
};

export default Footer;
