import emailjs from "emailjs-com";
import React from "react";
import { Container, FormWrap, Icon } from "./SigninElements";
import "../Signin/index.css";

export default function ContactUs() {
  function sendEmail(e) {
    e.preventDefault();

    emailjs
      .sendForm(
        "service_xr74tqp",
        "template_fdosx8y",
        e.target,
        "user_8T6ePwpMKmwPwHuipxyLt"
      )
      .then(
        (result) => {
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );
    e.target.reset();
  }

  return (
    <Container>
      <FormWrap>
      <Icon to="/">Return</Icon>
        <form onSubmit={sendEmail}>
          <div className="formcontent">
            <h1>Send me a message</h1>
            <div className="col-8 form-group mx-auto">
              <input
                type="text"
                className="form-control"
                placeholder="Name"
                name="name"
              />
            </div>
            <div className="col-8 form-group pt-2 mx-auto">
              <input
                type="email"
                className="form-control"
                placeholder="Email Address"
                name="email"
              />
            </div>
            <div className="col-8 form-group pt-2 mx-auto">
              <input
                type="text"
                className="form-control"
                placeholder="Subject"
                name="subject"
              />
            </div>
            <div className="col-8 form-group pt-2 mx-auto">
              <textarea
                className="form-control"
                id=""
                cols="30"
                rows="8"
                placeholder="Your message"
                name="message"
              ></textarea>
            </div>
            <div className="btn btn-info">
              <input
                type="submit"
                className="sendbutton"
                value="Send Message"
              ></input>
            </div>
          </div>
        </form>
      </FormWrap>
    </Container>
  );
}
